import Web3 from "web3";
import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";

import farm_abi from "../abis/MultiRewards.json";
import ierc20_abi from "../abis/IERC20.json";
import {formatWeiToNumber, toWei} from "../utils/format";
import CoineusLoading from "../components/CoineusLoading";
import {
    Avatar,
    Button,
    Grid,
    List,
    ListItem, ListItemAvatar,
    ListItemText,
    Paper,
} from "@mui/material";
import {CoineusCryptoFormat} from "../utils/currency_format";
import {NetworkButton} from "../components/Coineus";
import {CHAIN_IDS} from "../constants";
import PercentageArc from "../components/PercentageArc";
import RewardDetails from "../components/RewardsDetails";

const FARM_CONTRACT = "0xF793a9803c1260C470858cd20D75E37e5BF3C999";
const STAKE_TOKEN = "0xcC8A1364Bd05e05402e8b28dAe554b5d6216E6B9";//CHAD

const REWARD_TOKEN0 = "0x9f19c8e321bD14345b797d43E01f0eED030F5Bff";//SRG
const REWARD_TOKEN2 = "0x67B38309BACBc77E6570e9557b23d6955A9bD7e3";//PEPE

const updateTimeout = 10000;

export default function CHADBNBStaking() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const web3 = new Web3(rpc.bnb);
    const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);
    const stake_token = new web3.eth.Contract(ierc20_abi, STAKE_TOKEN)

    const [data, setData] = useState({});
    const [pending, setPending] = useState(false);
    const [amount, setAmount] = useState("");
    const [apy, setApy] = useState(0);

    const getInfo = () => {

        if (!wallet.address) return;

        const calls = [
            farm_contract.methods.totalSupply().call,
            farm_contract.methods.balanceOf(wallet.address).call,

            stake_token.methods.balanceOf(wallet.address).call,
            stake_token.methods.totalSupply().call,

            //reward 0
            farm_contract.methods.rewardData(REWARD_TOKEN0).call,
            farm_contract.methods.earned(wallet.address, REWARD_TOKEN0).call,
            farm_contract.methods.getRewardForDuration(REWARD_TOKEN0).call,

            //reward 2
            farm_contract.methods.rewardData(REWARD_TOKEN2).call,
            farm_contract.methods.earned(wallet.address, REWARD_TOKEN2).call,
            farm_contract.methods.getRewardForDuration(REWARD_TOKEN2).call,
        ]

        makeBatchRequest(web3, calls).then(resp => {
            const [
                totalStaked,
                stakedAmount,

                stake_token_balance,
                stake_token_supply,

                rewardTokenData0,
                earned0,
                getRewardForDuration0,

                rewardTokenData2,
                earned2,
                getRewardForDuration2,
            ] = resp;

            const secondsLeft0 = calcSecsLeft(rewardTokenData0.periodFinish);
            const rewardsRemaining0 = secondsLeft0 * rewardTokenData0.rewardRate / (10 ** 9);
            const potentialEarnings0 = rewardsRemaining0 * (stakedAmount / totalStaked);

            const secondsLeft2 = calcSecsLeft(rewardTokenData2.periodFinish);
            const rewardsRemaining2 = secondsLeft2 * rewardTokenData2.rewardRate / (10 ** 9);
            const potentialEarnings2 = rewardsRemaining2 * (stakedAmount / totalStaked);


            const value0 = rewardsRemaining0 ? getRewardForDuration0 / (10 ** 9) * wallet.prices.bnb?.[REWARD_TOKEN0]?.inUSD : 0;
            const value2 = rewardsRemaining2 ? getRewardForDuration2 / (10 ** 9) * wallet.prices.bnb?.[REWARD_TOKEN2]?.inUSD : 0;
            const valueLP = totalStaked / (10 ** 9) * wallet.prices.bnb?.[STAKE_TOKEN]?.inUSD
            const valueTotal = value0 + value2;

            setApy((valueTotal * 52) / valueLP * 100);

            setData({
                stake_token_supply: stake_token_supply / (10 ** 9),
                stake_token_balance: formatWeiToNumber(stake_token_balance, 9),
                totalStaked: formatWeiToNumber(totalStaked, 9),
                stakedAmount: formatWeiToNumber(stakedAmount, 9),

                reward0: {
                    contract: REWARD_TOKEN0,
                    pending: formatWeiToNumber(earned0, 9),
                    rewardsForDuration: formatWeiToNumber(getRewardForDuration0, 9),
                    rewardsRemaining: rewardsRemaining0,
                    potentialEarnings: potentialEarnings0,
                    periodFinish: rewardTokenData0.periodFinish * 1000,
                    decimals: 9
                },

                reward2: {
                    contract: REWARD_TOKEN2,
                    pending: formatWeiToNumber(earned2, 9),
                    rewardsForDuration: formatWeiToNumber(getRewardForDuration2, 9),
                    rewardsRemaining: rewardsRemaining2,
                    potentialEarnings: potentialEarnings2,
                    periodFinish: rewardTokenData2.periodFinish * 1000,
                    decimals: 9
                }
            });

        })
    }

    const calcSecsLeft = (periodFinish) => {
        const left = parseInt(((periodFinish * 1000) - new Date().getTime()) / 1000);

        return left > 0 ? left : 0;
    }

    /**
     * addRewards(rewardToken, managerAddress, duration) - add reward token
     * 2592000 = 30 days
     * 604800 = 7 days
     */

    const stake = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);

            const stake_token_contract = new web3.eth.Contract(ierc20_abi, STAKE_TOKEN);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);

            stake_token_contract
                .methods
                .allowance(wallet.address, FARM_CONTRACT)
                .call((err, resp) => {

                    if (err) return;

                    if (parseInt(resp) < parseInt(toWei(amount, 9))) {
                        stake_token_contract
                            .methods
                            .approve(FARM_CONTRACT, toWei(amount, 9))
                            .send({from: wallet.address})
                            .once('transactionHash', function (tx) {
                                setPending(true)
                            })
                            .on('error', function (error) {
                                setPending(false)
                            })
                            .then(function (receipt) {
                                setPending(false)
                                stake()
                            });
                    } else {
                        stake()
                    }
                })

            const stake = () => {
                farm_contract.methods.stake(toWei(amount, 9))
                    .send({from: wallet.address, gas: 700000})
                    .once('transactionHash', function (tx) {
                        setPending(true)
                    })
                    .on('error', function (error) {
                        setPending(false)
                    })
                    .then(function (receipt) {
                        setPending(false);
                        getInfo();
                    });
            }
        }
    }

    const getReward = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);
            farm_contract.methods.getReward()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getInfo();
                    setPending(false);
                });
        }
    }

    const exit = () => {
        if (wallet.address) {
            const web3 = new Web3(window.provider);
            const farm_contract = new web3.eth.Contract(farm_abi, FARM_CONTRACT);
            farm_contract.methods.exit()
                .send({from: wallet.address})
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getInfo();
                    setPending(false);
                });
        }
    }

    useEffect(() => {
        getInfo();
        const interval = setInterval(getInfo, updateTimeout)
        return () => clearInterval(interval);
    }, [wallet.address]);

    return <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
        <Grid item xs={12}>
            <Paper>
                <List>
                    <ListItem>
                        <ListItemAvatar>
                            <Avatar src={`https://coineus.app/assets/tokens/bnb/${STAKE_TOKEN}/logo.png`}/>
                        </ListItemAvatar>
                        <ListItemText
                            primary={CoineusCryptoFormat(data.totalStaked)}
                            secondary="Total CHAD Staked"
                        />
                        <div style={{marginTop: -15}}>
                            <PercentageArc
                                value={data.totalStaked}
                                total={data.stake_token_supply}
                                height={50}
                                label="% SUPPLY STAKED"
                            />
                        </div>
                    </ListItem>
                    <ListItem>
                        <ListItemText
                            primary={apy.toFixed(2) + "%"}
                            secondary="Est. APY"
                        />
                        <ListItemText
                            primary={CoineusCryptoFormat(data.stakedAmount)}
                            secondary="My Staked Amount"
                        />

                        <div style={{marginTop: -15}}>
                            <PercentageArc
                                value={data.stakedAmount}
                                total={data.totalStaked}
                                height={50}
                                label="MY STAKE SHARE"
                            />
                        </div>
                    </ListItem>
                    <ListItem>
                            <NetworkButton network={CHAIN_IDS.BNB}>
                                <Button
                                    variant="contained"
                                    fullWidth
                                    onClick={() => exit()}>Withdraw</Button>
                            </NetworkButton>
                    </ListItem>
                </List>
            </Paper>
        </Grid>

        <RewardDetails
            details={data.reward0}
            rewardPriceInUSD={wallet.prices.bnb?.[data.reward0?.contract]?.inUSD}
            rewardSymbol="SRG"
            network="bnb"
            farmContract={FARM_CONTRACT}
        />

        <RewardDetails
            details={data.reward2}
            rewardPriceInUSD={wallet.prices.bnb?.[data.reward2?.contract]?.inUSD}
            rewardSymbol="PEPE"
            network="bnb"
            farmContract={FARM_CONTRACT}
        />

        <Grid item xs={12}>
            <Paper>
                <List>
                    <ListItem>
                        <NetworkButton network={CHAIN_IDS.BNB}>
                            <Button
                                variant="contained"
                                fullWidth
                                onClick={() => getReward()}>Claim Rewards</Button>
                        </NetworkButton>
                    </ListItem>
                </List>
            </Paper>
        </Grid>

        <CoineusLoading open={pending} label="Transaction Pending"/>
    </Grid>
}